	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue'
export default{
	data(){
		return {
			username:"",
			password:"",
			msg:""
		}
	},
	components:{
		uniPopup,
	    uniPopupMessage
	},
	methods:{
		login(){
			uni.showLoading({
				title:'登录中'
			})
			var that=this;
			var app=getApp().globalData;
			var url=app.url;
			uni.request({
				url:url+'login2/tecLogin',
				method:"post",
				data:{
					userName:that.username,
					password:that.password
				},
				header: {
				        //设置参数内容类型为json
				        'Content-Type': 'application/x-www-form-urlencoded' // 默认值
				      },
			    dataType: 'json',
				success:(res)=>{
					uni.hideLoading();
					if(res.data.result){	
						app.token=res.data.data
						uni.switchTab({
							url:'../home/home',
						})
					}else{
						that.msg=res.data.msg;
						this.$refs.oftenProblem.open();
					}
				},
				fail:(res)=>{
					uni.hideLoading();
					that.msg="服务器异常!";
					this.$refs.oftenProblem.open();
				}
			})
		}
	}
}
